module.exports = "/* Navbar stil */\r\n.navbar {\r\n    background-color: #343a40; /* Navbar arkaplan rengi */\r\n    border-bottom: 4px solid #007bff; /* Alt çizgi rengi */\r\n  }\r\n/* Navbar linkleri için stil */\r\n.navbar-nav .nav-link {\r\n    color: #fff; /* Metin rengi */\r\n    font-weight: bold; /* Kalın metin */\r\n    padding: 15px 20px; /* Link içeriği ile sağ ve sol boşluklar */\r\n    transition: background-color 0.3s, color 0.3s; /* Renk değişimi animasyonu */\r\n  }\r\n/* Navbar linkleri üzerine gelindiğinde stil */\r\n.navbar-nav .nav-link:hover {\r\n    background-color: #007bff; /* Link üzerine gelindiğinde arkaplan rengi */\r\n    color: #fff; /* Link üzerine gelindiğinde metin rengi */\r\n  }\r\n/* Aktif navbar linki için stil */\r\n.navbar-nav .nav-item.active .nav-link {\r\n    background-color: #007bff; /* Aktif linkin arkaplan rengi */\r\n    color: #fff; /* Aktif linkin metin rengi */\r\n  }\r\n  \r\n/*# sourceMappingURL=data:application/json;base64, */"