module.exports = "/* Arama alanı stilini özelleştirme */\r\n.custom-search {\r\n  background-color: #27ae60; /* Mavi tonlu arka plan rengi */\r\n  border-radius: 5px;\r\n  padding: 10px;\r\n  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);\r\n}\r\n/* Tablo stilleri */\r\n.table {\r\n  background-color: #f0f0f0; /* Updated table background color */\r\n  border: 1px solid #ddd;\r\n  border-collapse: collapse;\r\n  width: 100%;\r\n}\r\n/* Tablo başlık stilleri */\r\n.table thead {\r\n  background-color: #007bff;\r\n  color: #fff;\r\n}\r\n/* Tablo başlık hücreleri */\r\n.table th {\r\n  padding: 12px;\r\n  text-align: center;\r\n  background-color: #0062cc; /* Updated header background color */\r\n  color: #fff;\r\n}\r\n/* Tablo veri hücreleri */\r\n.table td {\r\n  padding: 10px;\r\n  text-align: center;\r\n  border-top: 1px solid #ddd;\r\n}\r\n/* Zebra çizgili tablo (opsiyonel) */\r\n.table tbody tr:nth-child(even) {\r\n  background-color: #e0e0e0;\r\n}\r\n/* Hücre içeriği stilini özelleştirme (örnek olarak) */\r\n.table td {\r\n  color: #333;\r\n  background-color: #f0f0f0; /* Updated cell background color */\r\n}\r\n/* Seçili satır stilini özelleştirme (opsiyonel) */\r\n.table tbody tr.selected {\r\n  background-color: #ffffcc;\r\n}\r\n/* Düğme stilini özelleştirme */\r\n.action-button {\r\n  margin: 5px;\r\n  padding: 10px 20px;\r\n  font-size: 16px;\r\n  border-radius: 5px;\r\n  cursor: pointer;\r\n  transition: background-color 0.3s;\r\n}\r\n.action-button:disabled {\r\n  background-color: #ccc;\r\n  cursor: not-allowed;\r\n}\r\n.add-button {\r\n  background-color: #27ae60; /* Yeşil tonlu ekle düğmesi arka plan rengi */\r\n  color: #fff; /* Beyaz ekle düğmesi metin rengi */\r\n}\r\n.delete-button {\r\n  background-color: #e74c3c; /* Kırmızı tonlu sil düğmesi arka plan rengi */\r\n  color: #fff; /* Beyaz sil düğmesi metin rengi */\r\n}\r\n.edit-button {\r\n  background-color: #3498db; /* Mavi tonlu güncelle düğmesi arka plan rengi */\r\n  color: #fff; /* Beyaz güncelle d